import * as API from './';
/**
 * @description -------- 
 */

/**
 * @description -----  : status: 0- 1- 2-
 */
export const GetCurrentRoomRefund = orderId => API.GET(`/api/business/refund/orderRoom/${orderId}`, true)

/**
 * @description ----- 
 */
export const GetRefundDetail = refundRecordId => API.GET(`/api/business/refund/detail/${refundRecordId}`, true)

/**
 * @description ----- : status = 0 
 * @param {Object} data
 * @param {String} data.refundRecordId  GetCurrentRoomRefund  id
 */
export const PostReFundAgain = data => API.POST(`/api/business/refund/again/${data.refundRecordId}`)



